.callback {
  margin-top: 60px;
  &__wrapper {
    padding: 13px;
    border: 1px solid $white;
  }
  &__title {
    font-size: 32px;
    font-weight: 500;
    color: $white;
    line-height: 100%;
    text-transform: uppercase;
  }
  &__text {
    margin-top: 18px;
    color: $white;
    font-family: "Montserrat";
    font-size: 16px;
    line-height: 120%;
    @include screen(laptop) {
      max-width: 600px;
    }
  }
  &__form {
    margin-top: 24px;
    display: flex;
    flex-direction: column;
    @include screen(laptop) {
      align-items: flex-end;
    }
  }
  &__labels {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 8px;
    @include screen(laptop) {
      flex-direction: row;
      gap: 16px;
    }
  }
  &__label {
    width: 100%;
  }
  &__input {
    width: 100%;
    background-color: #1e1e1e;
    padding: 15px;
    border-bottom: 1px solid $white;
    font-family: "Inter";
    &::placeholder {
      font-weight: 500;
      font-size: 11px;
      line-height: 100%;
    }
  }
  &__submit {
    margin-top: 23px;
    background-color: $white;
    padding: 13px 60px;
    text-transform: uppercase;
    font-family: "Manrope";
    transition: $transition-default;
    @include screen(laptop) {
      width: calc(33.3334% - 11px);
    }
    &:hover {
      background-color: #cccccc;
    }
  }
}