.footer {
  padding: 60px 0 55px;
  &__wrapper {
    display: flex;
    flex-direction: column;
    gap: 76px;
    align-items: center;
    @include screen(laptop) {
      display: grid;
      grid-template-columns: repeat(3, minmax(0, 1fr));
    }
  }
  &__links {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    max-width: 288px;
    margin: 0 auto;
    gap: 23px;
  }
  &__link {
    font-size: 15px;
    line-height: 100%;
    font-family: "Manrope";
    color: $white;
    transition: $transition-default;
    &:hover {
      color: #cccccc;
    }
  }
  &__contacts {
    display: flex;
    flex-direction: column;
    gap: 23px;
    align-items: center;
    a {
      font-family: "Manrope";
      color: $white;
      line-height: 100%;
      font-size: 23px;
      font-weight: 500;
      transition: $transition-default;
      &:hover {
        color: #cccccc;
      }
    }
  }
  &__copy {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 8px;
    a {
      font-weight: 300;
      font-size: 11px;
      font-family: "Manrope";
      line-height: 100%;
      color: $white;
      transition: $transition-default;
      &:hover {
        color: #cccccc;
      }
    }
    p {
      font-weight: 300;
      font-family: "Manrope";
      font-size: 11px;
      line-height: 100%;
      color: $white;
    }
  }
}