.docs {
  margin-top: 60px;
  &__wrapper {
    padding: 18px 16px;
    border: 1px solid $white;
  }
  &__title {
    font-size: 20px;
    font-weight: 400;
    color: $white;
    line-height: 120%;
    font-family: "Montserrat";
  }
  &__items {
    margin-top: 24px;
    display: flex;
    flex-direction: column;
    gap: 21px;
  }
  &__item {
    display: flex;
    align-items: center;
    gap: 10px;
    transition: $transition-default;
    &:hover {
      span {
        color: #cccccc;
      }
    }
    img {
      min-width: 30px;
      min-height: 30px;
    }
    span {
      font-family: "Montserrat";
      font-size: 18px;
      color: $white;
      line-height: 120%;
    }
    &-small {
      span {
        font-size: 14px;
        @include screen(laptop) {
          font-size: 18px;
        }
      }
    }
  }
}